<template>
  <div id="SIDE_MODAL">
    <div v-if="dialog1" class="side-modal-wrapper">
      <div class="side-modal-mask" />
      <div class="side-modal">
        <div class="side-modal-header">
          <div class="ant-row-flex ant-row-flex-middle editable-text-field">
            <div class="ant-col ant-col-17 editable-text-col">
              <a-input placeholder="" v-model="node.name" />
            </div>
          </div>
        </div>
        <div class="side-modal-body">
          <div class="side-modal-body-content">
            <ul class="tab-tilte">
              <li :class="{ active: cur == 0 }" @click="cur = 0"></li>
              <li :class="{ active: cur == 1 }" @click="cur = 1"></li>
            </ul>
            <div class="tab-content">
              <div v-show="cur == 0">
                <a-row>
                  <a-col :span="6">
                    <a-button
                      type="primary"
                      @click="addRole('role', targetRoleKeys)"
                    >
                      
                    </a-button>
                  </a-col>
                </a-row>
                <a-row>
                  <a-button
                    v-for="(item, index) of targetRoleKeys"
                    :key="index + 'fdf'"
                    class="btn"
                    @click="addRole('', targetRoleKeys)"
                  >
                    {{ item }}
                  </a-button>
                </a-row>
                <a-row>
                  <a-col :span="6">
                    <a-button
                      type="primary"
                      @click="addRole('user', targetUserKeys)"
                    >
                      
                    </a-button>
                  </a-col>
                </a-row>
                <a-row>
                  <a-button
                    v-for="(item, index) of targetUserKeys"
                    :key="index + 'fdf'"
                    class="btn"
                    @click="addRole('', targetUserKeys)"
                  >
                    {{ item }}
                  </a-button>
                </a-row>
              </div>
              <div v-show="cur == 1" class="setting">
                <div>
                  <a-checkbox-group
                    class="width_100 display_f align_c justify_c_b"
                    @change="onChange"
                  >
                    <a-row :gutter="[0, 15]">
                      <a-col :span="1">
                        <a-checkbox :default-checked="false" value="" />
                      </a-col>
                      <a-col :span="4">
                        <span></span>
                      </a-col>
                      <a-col :span="19">
                        <a-input :value="submitVal" />
                      </a-col>
                    </a-row>
                    <a-row :gutter="[0, 15]">
                      <a-col :span="1">
                        <a-checkbox :default-checked="false" value="" />
                      </a-col>
                      <a-col :span="4">
                        <span></span>
                      </a-col>
                      <a-col :span="19">
                        <a-input placeholder="" :value="draftVal" />
                      </a-col>
                    </a-row>
                    <a-row :gutter="[0, 15]">
                      <a-col :span="1">
                        <a-checkbox :default-checked="false" value="" />
                      </a-col>
                      <a-col :span="4">
                        <span></span>
                      </a-col>
                      <a-col :span="19">
                        <a-input :value="recallVal" />
                      </a-col>
                    </a-row>
                    <a-row :gutter="[0, 15]">
                      <a-col :span="1">
                        <a-checkbox :default-checked="false" value="" />
                      </a-col>
                      <a-col :span="4">
                        <span></span>
                      </a-col>
                      <a-col :span="19">
                        <a-input :value="backVal" />
                      </a-col>
                    </a-row>
                    <a-row :gutter="[0, 15]">
                      <a-col :span="1">
                        <a-checkbox :default-checked="false" value="" />
                      </a-col>
                      <a-col :span="4">
                        <span></span>
                      </a-col>
                      <a-col :span="19">
                        <a-input :value="urgedVal" />
                      </a-col>
                    </a-row>
                  </a-checkbox-group>
                </div>
                <div>
                  <h2 class="t_left"></h2>
                  <a-row>
                    <a-radio-group
                      v-model="approval"
                      @change="approvalOnChange"
                    >
                      <a-radio value="or" class="radio_m">
                        
                      </a-radio>
                      <a-radio value="add" class="radio_m">
                        
                      </a-radio>
                    </a-radio-group>
                  </a-row>
                </div>
              </div>
            </div>
            <!-- <textarea
              type="text"
              :value="JSON.stringify(properties)"
              style="width: 100%; height: 100px; margin-top: 10px"
            /> -->
          </div>
        </div>

        <div class="side-modal-footer">
          <button type="button" class="ant-btn ant-btn-default" @click="cancel">
            <span> </span></button
          ><button type="button" class="ant-btn ant-btn-primary" @click="save">
            <span> </span>
          </button>
        </div>
      </div>
    </div>
    <addUserModal
      ref="addUserModal"
      :visible="visible"
      @selectFun="selectFun"
    />
  </div>
</template>
<script>
// import AModal from './../AModal/AModal'
import addUserModal from "../AddUserModal";
// import addRoleModal from '../addRoleModal'
export default {
  components: {
    // AModal]
    addUserModal,
    // addRoleModal
  },
  props: {
    dialog: {
      type: Boolean,
      default: false,
    },
    properties: {
      type: Object,
      default: undefined,
    },
    node: {
      type: Object,
      default: undefined,
    },
  },
  data: () => ({
    visible: false,
    Rolevisible: false,
    dialog1: false,
    targetRoleKeys: [], // 
    targetUserKeys: [], // 
    checkedValues: [], // 
    cur: 0, // tab
    showAddRole: false,
    selectRole: [],
    currentApp: "target_management",
    currentAction: "or",
    temp: {}, // node
    properties1: {
      type: "notifier",
      name: "",
      otherInfor: {
        userList: [],
        roleList: [],
      },
      setCheckType: [],
      setCheckVal: {
        submit: "",
        draft: "",
        recall: "",
        urged: "",
      },
    },
    submitVal: "",
    draftVal: "",
    recallVal: "",
    urgedVal: "",
    backVal: "",
    approval: "or", // 
    typeName: "", // 
  }),
  watch: {
    dialog(val) {
      this.dialog1 = val;
    },
    dialog1(val) {
      this.$emit("update:dialog", val);
    },
    properties: {
      handler(val) {
        console.log("=====ff===============", val);
      },
      deep: true,
    },
  },
  mounted() {
    console.log("", this.properties);

    if (this.properties) {
      this.properties1 = this.properties;
    } else {
      return false;
    }
    // // Object.assign(this.properties1, this.properties)
    // if (this.properties1) {
    if (!this.properties1.setCheckVal) return false;
    this.submitVal = !this.properties1.setCheckVal.submit
      ? this.submitVal
      : this.properties1.setCheckVal.submit;
    this.draftVal = !this.properties1.setCheckVal.draft
      ? this.draftVal
      : this.properties1.setCheckVal.draft;
    this.recallVal = !this.properties1.setCheckVal.recall
      ? this.recallVal
      : this.properties1.setCheckVal.recall;
    this.urgedVal = !this.properties1.setCheckVal.urged
      ? this.urgedVal
      : this.properties1.setCheckVal.urged;
    this.backVal = !this.properties1.setCheckVal.back
      ? this.backVal
      : this.properties1.setCheckVal.back;

    this.targetRoleKeys = this.properties1.otherInfor.roleList;
    this.targetUserKeys = this.properties1.otherInfor.userList;
    // this.init()
    Object.assign(this.temp, this.properties1);
    // }
  },
  methods: {
    // 
    addRole(type, _targetKeys) {
      this.$refs.addUserModal.getAddType(type, _targetKeys);
      this.visible = true;
    },
    onClose() {
      this.dialog1 = false;
    },
    onChange(checkedValues) {
      this.checkedValues = checkedValues;
    },
    approvalOnChange(e) {
      this.approval = e.target.value;
      console.log("radio checked", this.approval);
    },
    init() {},
    // 
    save() {
      var setCheckType = this.checkedValues;
      var otherInfor = {
        userList: this.targetUserKeys,
        roleList: this.targetRoleKeys,
      };
      this.dialog1 = false;
      this.properties1.name = this.targetUserKeys;
      this.properties1.otherInfor = otherInfor;
      this.properties1.setCheckType = setCheckType;
      if (this.properties.type !== "start") {
        this.properties1.approval = this.approval;
      }
      Object.assign(this.temp, this.properties1);
      this.$emit("setProperties", this.properties1);
    },
    cancel() {
      this.dialog1 = false;
      this.properties1 = {};
      Object.assign(this.properties1, this.temp);
      // this.init()
      this.$emit("setProperties", this.properties1);
    },
    // key
    selectFun(issave, val, type) {
      this.visible = !this.visible;
      if (!issave) return;
      this.selectRole = [...val];
      this.targetKeys = this.selectRole;
      switch (type) {
        case "user":
          this.targetUserKeys = this.selectRole;
          break;
        default:
          this.targetRoleKeys = this.selectRole;
          break;
      }
      console.log("", type, this.selectRole);
    },
  },
};
</script>

<style scoped>
ul {
  display: block;
  width: 100%;
  overflow: hidden;
  border-bottom: 1px solid #ccc;
}
ul li {
  display: block;
  margin: 0;
  padding: 0;
  list-style: none;
}
.tab-tilte {
  width: 100%;
}
.tab-tilte li {
  float: left;
  width: 25%;
  padding: 10px 0;
  text-align: center;
  cursor: pointer;
}
/*  */
.tab-tilte .active {
  border-bottom: 2px solid #09f;
  color: #09f;
}
.tab-content div button {
  margin-bottom: 15px;
  margin-right: 10px;
}
/* .setting {
  display: flex;
} */
.radio_m {
  display: block;
  height: 30px;
  line-height: 30px;
  text-align: left;
}
</style>